* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  font-family: Poppins, sans-serif;
}

header {
  width: 100%;
  background: #f0f3f4;
  padding: 12px 40px;
}

ul {
  list-style: none;
}

a {
  color: #17201f;
  text-decoration: none;
}

button {
  font-family: Poppins, sans-serif;
}

.login__info {
  flex-direction: column;
  display: flex;
}

.forms__titles {
  text-align: center;
  color: #17201f;
  margin-bottom: 20px;
  font-size: 1.25em;
  font-style: normal;
  font-weight: normal;
}

.forms {
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
  display: flex;
}

.forms__inputs {
  width: 360px;
  height: 64px;
  box-sizing: border-box;
  background: none;
  border: 1px solid #17201f;
  border-radius: 5px;
  margin-bottom: 16px;
  padding-left: 25px;
  font-family: Poppins;
  font-size: 1.25em;
  font-style: normal;
  font-weight: normal;
}

.forms__inputs::placeholder {
  color: #93b1b180;
}

.forms__btn {
  width: 360px;
  height: 64px;
  box-sizing: border-box;
  color: #f0f3f4;
  background: #93b1b1;
  border: 1px solid #17201f;
  border-radius: 5px;
  font-family: Poppins;
  font-size: 1.125em;
  font-style: normal;
  font-weight: normal;
}

